html, body, #root{
  width: 100%;
  height: 100%;
  background: #fff;
}

.page{
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 49px;
  background: #fff;
  &.subpage{
    bottom: 0;
    z-index: 100;
  }
}

.textflow{
 white-space: nowrap;
 text-overflow: ellipsis;
 overflow: hidden;
}

.border-top{
  border-top: 0.5px solid #ddd;
}

.border-bottom{
  border-bottom: 0.5px solid #ddd;
}